import React from "react"
import classnames from "classnames"
import styled from "styled-components"

const WrapSelect = styled.div`
  padding: 0rem 0px;
  & .option {
    color: #575757;
    min-height: 30px;
    display: flex;
    align-items: center;
    padding: 0.5rem 1rem;
    cursor: pointer;
    border-bottom: 1px solid #e9ecef;
  }
  & .option:last-child {
    border-bottom: 0px;
  }
  & .option:hover {
    color: #546c84;
    background-color: #fbfbfb;
  }
  & .option.selected {
    background: #10467a;
    color: white;
    border-radius: 4px;
  }
`
export default function MUISelectBox(props) {
  const { sourceData, valueSelected, onChange } = props
  console.warn("MUISelectBox:", sourceData, valueSelected)
  return (
    <WrapSelect className="mui-select_box">
      {sourceData &&
        sourceData.map((v, i) => {
          return (
            <div
              key={i}
              className={classnames("option", {
                selected: v.Value == valueSelected,
              })}
              onClick={() => {
                if (onChange) {
                  onChange(v)
                }
              }}
            >
              {v.Text}
            </div>
          )
        })}
    </WrapSelect>
  )
}
